.trending-categories {
  display: flex;
  flex-wrap: wrap;
  max-width: 85%;
  min-width: 1024px;
  margin: 0 auto;
  justify-content: space-evenly;
  
}

.category-item {
  width: 24%;
  height: 8rem;
  border-radius: 0.5rem;
  margin-top: 1%;
  background-color: var(--mainboard-color);
  padding: 2rem;
  text-align: left;
  cursor: pointer;
}

.category-item:hover {
  opacity: 0.5;
}